<script>
import axios from '../../node_modules/axios'
export default {
	name: 'HelloWorld',
	errors: [],
	props: {
		msg: String
	},
	data: function() {
		return {
			projects: []
		}
	},
	mounted () {
		axios.get('./json/project-data.json')
			.then( res => ( this.projects = res.data.projects ) )
			.catch( error => { this.errors = error } )
	},
	computed: {
		computedProjects() {
			return this.projects
		}
	}
}
</script>
<template>
	<div class="hello" v-if="projects">
		<div id="list-projects">
			<h2>{{ msg }}</h2>

			<div class="row" v-for="i in Math.ceil(projects.length / 3)" v-bind:key="i">
				<span v-for="project in projects.slice((i - 1) * 3, i * 3)" v-bind:key="project.id" class="col-4">

					<div class="panelHeading"><h4>{{ project.name }}</h4></div>                                    
					<div class="panelBody flexycolumn">
						<img src="img/graphingapp.jpg" width="100%" class="image-panel-bg" />
						<p>{{ project.description }}</p>
						<p>
							<span class="flexybutton">
								<a class="btn btn-primary btn-sm" :href="project.link" role="button">
									<div v-if="project.type=='private'"> 🔒 Details unavailable</div>
									<div v-if="project.type=='public'"> 🔓 View details &raquo;</div>
								</a>
							</span>
						</p>
					</div>

				</span>
			</div>

		</div>
	</div>
</template>
